




































export default {
	data() {
		return {
			height: '100%'
		}
	},
	onShareAppMessage(res) {
		return {
			title: '安徽博研认证',
			path: '/pages/index/index'
		}
	},
	onLoad() {
		this.getPhoneH()
	},
	methods: {
		// 电话质询
		callHanlde(phone) {
			uni.makePhoneCall({
				phoneNumber: phone //仅为示例
			});
		},
		// 获取屏幕高
		getPhoneH() {
			let that = this
			uni.getSystemInfo({
				success: function(res) {
					that.height = uni.upx2px(res.windowHeight * 2 - 120) + 'px'
				}
			});
		},
		// 查看地理位置
		getLocation() {
			uni.openLocation({
				latitude: 31.8851390000,
				longitude: 117.3116990000,
				success: function() {

				}
			});
		}
	}
}
